.hero{
    display: flex;
    justify-content: space-between;
 
}

.logo{
    width: 6rem;
    height: 2rem;


}
a{
    text-decoration: none;
    color: white;
    font-size: 1rem;
}
a:hover{
    color: orange;
}
.header-menu{
    list-style: none;
    display: inline-flex;
    gap: 2rem;
    color: white; 
    
}

.header-menu>li:hover{
    cursor: pointer;
    color: var(--orange);
}